@charset "UTF-8";
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a,
a:visited,
a:hover {
  color: inherit;
  text-decoration: none;
  outline: none;
}

@font-face {
  font-family: "NotoSans";
  src: url("../fonts/NotoSans-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "NotoSans";
  src: url("../fonts/NotoSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "CormorantInfant";
  src: url("../fonts/CormorantInfant-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "CormorantInfant";
  src: url("../fonts/CormorantInfant-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "NotoSans", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
}

.heading {
  font-family: "CormorantInfant", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.2;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #F6F6F6;
  color: #2F2F2F;
}

html,
body {
  height: 100%;
  margin: 0;
}

ul, li {
  padding: 0;
}

h1, h2, h3 {
  margin: 0;
}

input {
  outline: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  /*  flex-grow - увеличиваться в размере: 0 зап-т, 1 раз-т
  * flex-shrink - уменьшаться в размере: 0 зап-т, 1 раз-т
  * flex-basis - определяет размер элемента по умолчанию
  * */
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

[class*=__container] {
  max-width: 1030px;
  margin: 0 auto;
  padding: 0 15px;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo--header {
  height: 50px;
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}

.btn {
  align-self: center;
  background: linear-gradient(90deg, #FFE259 0%, transparent) #FFA751;
  padding: 20px 74px;
  color: #2F2F2F;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;
  transition: background-color 1s;
}
.btn-subscr {
  padding: 13px 29px;
  color: #FFE058;
  text-transform: uppercase;
  background: #2F2F2F;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  display: flex;
  align-items: center;
}
.btn-image {
  width: 25px;
  height: 25px;
  display: inline-block;
  margin-left: 10px;
  transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26);
}
.btn__arrow {
  position: absolute;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
}
.btn__arrow-svg {
  width: 40px;
  height: 40px;
}

.unvisible {
  position: absolute;
  left: -3000px;
  overflow: hidden;
  width: 0;
  height: 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 0;
}
.header__right {
  display: flex;
  align-items: center;
}
.header__search {
  display: flex;
  align-items: center;
  position: relative;
  height: 50px;
  margin-left: 50px;
}
.header__search-btn {
  height: 24px;
  width: 24px;
  background: url(../images/icons/sprite.svg#search) center no-repeat;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(-50%);
  z-index: 2;
  transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26);
  cursor: pointer;
}
.header__search-input {
  background-color: transparent;
  border: none;
  outline: none;
  height: 100%;
  width: 24px;
  transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26);
  z-index: 3;
  cursor: pointer;
}
.header__search-input:focus {
  width: 200px;
  padding: 10px 30px 10px 20px;
  color: #2F2F2F;
  background-color: #fff;
  cursor: auto;
}
.header__search-input:focus + .header__search-btn {
  z-index: 4;
  right: 5px;
}

.menu__body {
  margin-top: -5px;
}
@media (max-width: 768px) {
  .menu__body {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    padding: 150px 30px 50px 70px;
    background-color: rgba(246, 246, 246, 0.9);
    overflow: auto;
    transition: right 0.5s ease 0s;
  }
  .menu__body::before {
    content: "";
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 74px;
    background-color: #F6F6F6;
    transition: right 0.5s ease 0s;
    z-index: 2;
  }
}
.menu__body._active {
  right: 0;
}
.menu__body._active::before {
  right: 0;
}
.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 768px) {
  .menu__list {
    flex-direction: column;
    align-items: flex-start;
  }
}
.menu__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #2F2F2F;
  text-transform: uppercase;
  transition: color 0.3s ease;
}
.menu__item:not(:last-child) {
  margin-right: 50px;
}
.menu__item::before {
  position: absolute;
  content: "";
  display: block;
  bottom: -6px;
  width: 0%;
  height: 2px;
  background-color: #2F2F2F;
  transition: width 0.5s ease;
}
.menu__item:last-child a {
  transition: 0.3s linear;
}
@media (max-width: 768px) {
  .menu__item {
    margin-bottom: 40px;
    font-weight: 800;
    font-size: 18px;
  }
  .menu__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.menu__icon {
  display: none;
}
@media (max-width: 768px) {
  .menu__icon {
    display: block;
    position: relative;
    flex: 0 0 32px;
    width: 32px;
    height: 29px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    z-index: 5;
  }
}
.menu__icon span {
  position: absolute;
  top: calc(50% - 3px);
  left: 0;
  width: 100%;
  height: 6px;
  background-color: #FFFFFF;
  transition: all 0.3s ease 0s;
}
.menu__icon span:first-child {
  top: 0;
}
.menu__icon span:last-child {
  top: auto;
  bottom: 0;
}
.menu__icon._active span {
  transform: scale(0);
}
.menu__icon._active span:first-child {
  top: calc(50% - 3px);
  transform: rotate(-45deg);
}
.menu__icon._active span:last-child {
  top: calc(50% - 3px);
  transform: rotate(45deg);
}

.hero {
  height: 800px;
  background: #000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.hero::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.hero__container {
  display: flex;
  align-items: center;
  height: 100%;
}
.hero__wrp {
  max-width: 686px;
  color: #FFFFFF;
  z-index: 2;
}
.hero__head {
  font-weight: 700;
  font-size: 100px;
  line-height: 1.2;
  margin-bottom: 15px;
}
.hero__descr {
  font-weight: 400;
  font-size: 25px;
  line-height: 1.2;
}
.hero__descr span {
  font-family: "CormorantInfant";
  margin-top: 7px;
  display: inline-block;
}
.hero__btn {
  margin-top: 20px;
}

@media (min-width: 992px) {
  .btn:hover, .btn:focus {
    background-color: #FFE058;
  }

  .menu__item:hover::before {
    width: 100%;
  }
  .btn-subscr:hover .btn-image {
    margin-left: 15px;
    margin-right: 7px;
  }

  .btn-subscr:hover {
    background-color: #2F2F2F;
  }
}
@media (min-width: 992px) {
  .btn__arrow {
    animation: move-down 5s infinite linear;
  }
}
@keyframes move-down {
  0% {
    transform: translateY(0px) scale(1);
  }
  30% {
    transform: translateY(30px) scale(1.05);
  }
  45% {
    transform: translateY(0px) scale(1);
  }
  70% {
    transform: translateY(30px) scale(1.05);
  }
  90% {
    transform: translateY(10px) scale(1.02);
  }
  100% {
    transform: translateY(0px) scale(1);
  }
}